import React, { useState } from "react";
import data from "./final.json";
import SearchBar from "./SearchBar";
import SearchResults from "./SearchResults";
import NavigationBar from "./NavigationBar";
import './App.css';
import Footer from "./Footer";

const App = () => {
  const [filteredResults, setFilteredResults] = useState([]);
  const[searchPerformed, setSearchPerformed] = useState(false);

  const handleSearch = (query, category) => {
    setSearchPerformed(true);
    if (query.trim() === "" && category === "0") {
      setFilteredResults([]);
      return;
    }

    const results = [];

    data.forEach((journal) => {
      const isCategoryMatch = category === "0" || journal.sinta === category; // Filter kategori
      if (isCategoryMatch) {
        journal.issue.forEach((issue) => {
          issue.article.forEach((article) => {
            if (article.title.toLowerCase().includes(query.toLowerCase())) {
              results.push({
                journal: journal.jurnal,
                sinta: journal.sinta,
                issue: issue.name,
                year: issue.tahun,
                title: article.title,
                link: article.link_article
              });
            }
          });
        });
      }
    });

    setFilteredResults(results);
  };

  return (
    <div>
      <NavigationBar />
      <div className="spaced-content">
        <SearchBar onSearch={handleSearch} />
        <SearchResults results={filteredResults} searchPerformed={searchPerformed}/>
      </div>
      <Footer />
    </div>
  );
};

export default App;
