// export default SearchResults;

import React, { useState, useEffect } from "react";

const SearchResults = ({ results, searchPerformed }) => {
  const [currentPage, setCurrentPage] = useState(1);
  const resultsPerPage = 5; // Jumlah hasil per halaman

  // Mengatur ulang halaman ke-1 saat hasil pencarian berubah
  useEffect(() => {
    setCurrentPage(1);
  }, [results]);

  const indexOfLastResult = currentPage * resultsPerPage;
  const indexOfFirstResult = indexOfLastResult - resultsPerPage;
  const currentResults = results.slice(indexOfFirstResult, indexOfLastResult);

  const totalPages = Math.ceil(results.length / resultsPerPage);

  const handlePrevPage = () => {
    if (currentPage > 1) setCurrentPage(currentPage - 1);
  };

  const handleNextPage = () => {
    if (currentPage < totalPages) setCurrentPage(currentPage + 1);
  };

  return (
    <div style={{ margin: "20px", textAlign: "left", paddingBottom: "80px"}}>
      {currentResults.length > 0 ? (
        currentResults.map((item, index) => (
          <div
            key={index}
            style={{
              border: "1px solid #ccc",
              margin: "10px auto",
              padding: "10px",
              maxWidth: "800px",
              width: "100%",
              borderRadius: "8px",
            }}
          >
            <h3 style={{ fontSize: "16px", fontFamily: "'Poppins', sans-serif" }}>
              <a
                href={item.link}
                style={{
                  textDecoration: "underline",
                  color: "black",
                  fontWeight: "500",
                }}
                target="_blank"
                rel="noopener noreferrer"
              >
                {item.title}
              </a>
            </h3>
            <p>Journal: {item.journal} ({item.sinta}) | Issue: {item.issue}</p>
          </div>
        ))
      ) : (
        searchPerformed && (
          <div style={{ textAlign: "center" }}>
            <p style={{ fontSize: "14px", fontFamily: "'Poppins', sans-serif", color: "black", marginTop: "20px" }}>
              No results found.
            </p>
          </div>
        )
      )}

      {/* Pagination Controls */}
      {results.length > resultsPerPage && (
        <div style={{ textAlign: "center", marginTop: "20px" }}>
          <button
            onClick={handlePrevPage}
            disabled={currentPage === 1}
            style={{
              padding: "8px 16px",
              margin: "0 5px",
              backgroundColor: currentPage === 1 ? "#ccc" : "#001F54",
              color: "white",
              border: "none",
              borderRadius: "5px",
              cursor: currentPage === 1 ? "default" : "pointer",
            }}
          >
            Previous
          </button>

          <span style={{ margin: "0 10px", fontWeight: "bold" }}>
            Page {currentPage} of {totalPages}
          </span>

          <button
            onClick={handleNextPage}
            disabled={currentPage === totalPages}
            style={{
              padding: "8px 16px",
              margin: "0 5px",
              backgroundColor: currentPage === totalPages ? "#ccc" : "#001F54",
              color: "white",
              border: "none",
              borderRadius: "5px",
              cursor: currentPage === totalPages ? "default" : "pointer",
            }}
          >
            Next
          </button>
        </div>
      )}
    </div>
  );
};

export default SearchResults;
