import React from 'react';
import { Navbar, Container } from 'react-bootstrap';
import './Navbar.css'; // Import file CSS untuk styling
import logo from './uin-jambi-logo.png';

const NavigationBar = () => {
  return (
    <Navbar className="custom-navbar" style={{ padding: '10px 20px' }}>
      <Container fluid style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        
        {/* Logo di ujung kiri */}
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <img
            src={logo}
            alt="Logo OSJ"
            style={{ width: '35px', height: '32px' }}
          />
        </div>

        {/* Teks di tengah */}
        <Navbar.Brand className="text-white" style={{ position: 'absolute', left: '50%', transform: 'translateX(-50%)' }}>
          OSJ Bidang Komputer
        </Navbar.Brand>
        
      </Container>
    </Navbar>
  );
};

export default NavigationBar;
